$black: #0d1b2a;
$darkblue: #1b263b;
$blue: #415a77;
$bold: 600;
$lightblue: #778da9;
$platinum: #e0e1dd;

/*HEADER*/
header{
    width: 80%;
    max-width: 800px;
    height: 15%;
    border: 15px solid;
    border-color: $darkblue;    
    padding: 3% 15%;    
    margin-bottom: 5%;  
    display: block;
    margin-left: auto;
    margin-right: auto;  
}

header h1{
    display: block;
    text-align: center;
    font-weight: 800;
    color: $darkblue;
    text-shadow: -1px 1px 0 $darkblue,
    2px 2px 0 $blue,
    2px -2px 0 $blue,
    -1px -1px 0 $blue;
}
  
  /*FRÅN TIDIGARE PROJEKT, SKA ÄNDRAS*/
  /*MUS, LÄNKAR HOVER, AFTER*/


/*HEADER INDEX*/
.header {
  background-color: rgba(247, 247, 239, 0.8);
  padding-left: 50px;
  padding-right: 50px;
  padding-top: 15px;
  padding-bottom: 15px;
  text-align: center;
  color: #2b2a2a;
}

/*HEADERS*/
.header h1 {
  font-family: "sacramento";
  font-size: 56px;
  color: #2b2a2a;
  line-height: 0.8;
}

/* Meny */
nav {
    background-color: $darkblue;
    box-shadow: $lightblue;
    font-size: 1.4em;
    position: fixed;
    right: 0;
    top: 0;
    height: 100vh;
    width: 250px;
    display: hidden;
    font-weight: $bold;
  
  
  ul {
    /*width: 100%;*/
    list-style-type: none;
    text-align: center;
    margin: 0 0 0 auto;
    padding: 0;
  }
  
  li {
    padding: 0.8em 0;
    /*width: 100%;*/
    
    
    
  }
  
  a {
    /*width: 100%;*/
    text-decoration: none;
    padding: 0 1em;
   
   
  }

}

 
  /*
  nav li:hover {
    background-color: $dark-pink;
    color: white;
    cursor: pointer;
  }*/

  nav a:hover{
    background-color: $lightblue;
    color: $platinum;
    cursor: pointer;
  }

  nav li:visited{
    font-style: italic;
    color: $black;
  }

  nav a:visited{
    color:$black;
    font-style: italic;
  }
  
  /*Meny-knapp, öppnameny*/
  .menu-btn {
    display: flex;
    align-items: center;
    padding: 8px 15px;
    background-color: $darkblue;  
    border: $blue solid 2px;
    color: $platinum;
    cursor: pointer;
    font-weight: 600;
  }
  
  .open {
    margin: auto auto 5px auto;
    width: 150px;
    padding: auto;
    display: block;
  }
  
  .menu-btn:hover, .menu-text:hover {
    background-color: $lightblue;
    color: $black;
    box-shadow: 0 0px 5px rgba(0, 0, 0, 0.421);
  }

  /*INDEX - INNEHÅLL*/
.flexbox1 {
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    font-weight: bold;
    width: 100%;
    margin-top: 50px;
    margin-bottom: 50px;
  }

@mixin box($color){
    flex-basis: 100%;
    width: 200px;
    height: 100px;
    font-size: 32px;
    padding-top: 50px;
    text-align: center;
    border-radius: 5%;
    margin: 30px;
    background-color: $color;
  }
  
  .main1{
    @include box($darkblue); 
  }
  
  /*
  .main1:hover ~ .image{
    background-image: url('/src/css/background/background-rosa.jpg');
  }*/
  
  /*LÄNK TILL TJÄNSTER*/
  #mainstart1 {
    @include box($lightblue); 
  }
  
